import React from "react";
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import poseListData from '../data/PoseData';
import Link from '@mui/material/Link';

export default function PoseList(props) {
    const { category } = props;
    return (
        <>
            <ImageList cols={3}>
                {poseListData
                    .filter((item) => item.category === category)
                    .map((item, i) => (
                        <ImageListItem className='col30  poselists' key={item.id}>
                            <Link href={`/yogiyoginibuild/topics/${item.id}`} underline="none">
                                <img
                                    src={`${item.img}?w=164&h=164&fit=crop&auto=format`}
                                    srcSet={`${item.img}?w=164&h=164&fit=crop&auto=format&dpr=2 2x`}
                                    alt={item.title}
                                    loading="lazy"
                                />
                                <ImageListItemBar
                                    className='pl-10 pr-10 fc-gray'
                                    title={<span className='card-list-title'>{item.title}</span>}
                                    subtitle={<span>{item.krtitle}</span>}
                                    position="below"
                                />
                            </Link>
                        </ImageListItem>
                    ))}
            </ImageList>
        </>
    );
}



