import React from 'react';
import { useParams } from 'react-router-dom';

import Container from '@mui/material/Container';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';

import poseListData from '../data/PoseData';

export default function Topic() {
    const dot = '.';

    var params = useParams();
    var topicid = params.topicid;
    var selectedtopic = {
        title: '죄송합니다.',
        description: '페이지를 찾을 수 없습니다.'
    }
    for (var i = 0; i < poseListData.length; i++) {

        if (poseListData[i].id === Number(topicid)) {
            selectedtopic = poseListData[i];
            break;
        }
    }

    return (
        <div>
            <Container component="main" maxWidth="md" sx={{ mt: 10, mb: 10 }}>
                <CardMedia
                    sx={{ height: 500 }}
                    image={dot + selectedtopic.img}
                    title="아사나"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div" className='card-list-title'>
                        {selectedtopic.title}
                    </Typography>
                    <Typography gutterBottom variant="h6" component="div" >
                        {selectedtopic.krtitle}
                    </Typography>
                    {/* <Typography variant="body2" color="text.secondary">
                        {selectedtopic.description}
                    </Typography> */}
                </CardContent>
            </Container>
        </div>
    );
}


<poseListData />