import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import PoseList from './PoseList';

function TabPanel(props) {
    const { children, value, index, ...other } = props;
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

export default function PoseTab() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ width: '100%' }}>
            <Box>

                {/* <Slider /> */}


                <Tabs
                    value={value}
                    onChange={handleChange}
                    variant="scrollable"
                    scrollButtons="auto"
                    aria-label="scrollable auto tabs example"
                >
                    <Tab className='ml-10' label="All" {...a11yProps(0)} />
                    <Tab className='ml-10' label="Surya Namaskara A" {...a11yProps(1)} />
                    <Tab className='ml-10' label="Surya Namaskara B" {...a11yProps(2)} />
                    <Tab className='ml-10' label="Standing" {...a11yProps(3)} />
                    <Tab className='ml-10' label="Seated" {...a11yProps(4)} />
                    <Tab className='ml-10' label="Finishing" {...a11yProps(5)} />
                </Tabs>
            </Box>
            <Typography color='deepgray'>
                <TabPanel value={value} index={0} >
                    <h3 className='p-20 pt-40' >Surya Namaskara A (12) </h3>
                    <PoseList category="suryaa" />
                    <h3 className='p-20 pt-40'>Surya Namaskara B (21) </h3>
                    <PoseList category="suryab" />
                    <h3 className='p-20 pt-40'>Standing (18) </h3>
                    <PoseList category="standing" />
                    <h3 className='p-20 pt-40'>Seated (32) </h3>
                    <PoseList category="seated" />
                    <h3 className='p-20 pt-40'>Finishing (15) </h3>
                    <PoseList category="finishing" />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <h3 className='p-20 pt-40' >Surya Namaskara A (12) </h3>
                    <PoseList category="suryaa" />
                </TabPanel>
                <TabPanel value={value} index={2}>
                    <h3 className='p-20 pt-40'>Surya Namaskara B (21) </h3>
                    <PoseList category="suryab" />
                </TabPanel>
                <TabPanel value={value} index={3}>
                    <h3 className='p-20 pt-40'>Standing (18) </h3>
                    <PoseList category="standing" />
                </TabPanel>
                <TabPanel value={value} index={4}>
                    <h3 className='p-20 pt-40'>Seated (32) </h3>
                    <PoseList category="seated" />
                </TabPanel>
                <TabPanel value={value} index={5}>
                    <h3 className='p-20 pt-40'>Finishing (15) </h3>
                    <PoseList category="finishing" />
                </TabPanel>
            </Typography>
        </Box>
    );
}

