import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Notfound from './pages/common/Notfound';
import Main from './pages/home/Main'
import PrivacyPolicy from './pages/home/PrivacyPolicy'

import { ThemeProvider } from '@mui/material/styles';
import theme from './theme/Theme';

import Topics from './components/Topics';
import Slider from './components/TopicSlide';


function App() {
  <theme />

  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route path="/" exact component={Main} />
          <Route path="/yogiyoginibuild/privacypolicy" exact component={PrivacyPolicy} />
          <Route path="/yogiyoginibuild/main" exact component={Main} />
          <Route path="/topics"><Topics /></Route>
          <Route path="/yogiyoginibuild/topicslide"><Slider /></Route>
          <Route path="*" component={Notfound} />
        </Switch>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;